.leader-board {
  display: flex;
  flex-direction: column;
  padding: 20px;
}
.board-header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  background-color: rgba(0, 0, 0, 0.288);
  align-items: center;
}

.board-body {
  display: flex;
  flex-direction: column;
}

.board-title {
  font-family: "Brothers";
  font-weight: bold;
  color: white;
}

.leader-item-cell {
  padding: 15px;
  text-align: center;
  /* font-size: 20px; */
  font-family: "Brothers";
  font-weight: bold;
  color: white;
}

.leader-item-row {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.leader-item-row:hover {
  background-color: rgba(116, 111, 111, 0.363);
}

.m-pagenavigation {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.board-fooder {
  display: flex;
  justify-content: right;
  padding: 5px;
  text-align: center;
  align-items: center;
  font-family: "Brothers";
  font-weight: bold;
  color: white;
}

.badge-online {
  width: 17px;
  height: 17px;
  border-radius: 50%;
  background-color: rgb(0, 245, 41);
}

.badge-block {
  width: 17px;
  height: 17px;
  border-radius: 50%;
  background-color: rgb(245, 16, 0);
}
